.dp-navigation-container {
  //border: 5px solid deeppink;
  width: 150px;
  display: flex;
  flex-direction: column;
  height: 100vh;
  padding-top: $dp-spacer-md;
  background: #343434;
}

// Content
.dp-content-wrapper {
  padding: 250px 0 250px 250px;
  display: flex;
  overflow: hidden;
  width: 100vw;

  .dp-page {
    display: flex;
    justify-content: center;
    font-size: 80px;
    line-height: 1.3;
    width: 100%;

  }
}

.dp-content-container {
  display: flex;
  flex: 3 0;
  padding: 0 $dp-spacer-md;
}

.dp-content {
  padding: 0 25px;

  @media screen and (min-width: $screen-sm-min) {
    display: flex;
    flex-direction: column;
    width: 100%;
    align-items: center;
    justify-content: center;
  }
}